import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchproduct } from "./rtk/slice/Slice_product";
import { addToCart } from "./rtk/slice/Cart_slice";
import Swal from "sweetalert2";

import "./index.css";
import { Link, useParams } from "react-router-dom";
const Product = () => {
  const alrat = (product) => {
    Swal.fire("Good job!", "Item placed successfully!", "success");
    dispatch(addToCart(product));
  };

  const product = useSelector((state) => state.products);
  const dispatch = useDispatch();
  const [allproduct, setallproduct] = useState([]);
  const [categories, setcategories] = useState([]);
  const [looding, setlooding] = useState(true);
  const ulr = "https://fakestoreapi.com/products";
  const Allproduct = () => {
    fetch(ulr)
      .then((res) => res.json())
      .then((json) => setallproduct(json));
    setlooding(false);
  };
  const categoriess = () => {
    fetch(`${ulr}/categories`)
      .then((res) => res.json())
      .then((json) => setcategories(json));
  };
  const jewelery = (jewelery) => {
    fetch(`${ulr}/category/${jewelery}`)
      .then((res) => res.json())
      .then((json) => setallproduct(json));
  };
  useEffect(() => {
    Allproduct();
    dispatch(fetchproduct());
    categoriess();
  }, []);

  return (
    <div className="container">
      <div className="pt-5">
        <div className="text-center " key={product.id}>
          <button
            onClick={() => {
              Allproduct();
            }}
            className="btn  m-2 all "
          >
            ALL
          </button>
          {categories.map((cat) => {
            return (
              <>
                <button
                  onClick={() => {
                    jewelery(cat);
                  }}
                  key={cat}
                  className="btn buttons m-2 "
                >
                  {cat}
                </button>
              </>
            );
          })}
        </div>
      </div>
      <div className="row">
        {allproduct.map((product) => {
          return (
            <>
              <div
                className="col-lg-3 col-md-6 col-sm-12 pt-5"
                key={product.id}
              >
                <div className="articles" id="articles">
                  <div className="container ">
                    <div className="box">
                      <Link to={`products/${product.id}`}>
                        <img
                          src={product.image}
                          className="card-img-top img-product"
                          alt={product.title}
                          width="250px"
                          height="250px"
                        />
                        <div className="content">
                          <h3 className="card-title mb-0">
                            {product.title.substring(0, 12)}...
                          </h3>
                          <p className="card-text lead fw-bold">
                            ${product.price}
                          </p>
                        </div>
                      </Link>
                      <div className="info">
                        <a
                          className="btn btn-outline-dark"
                          onClick={() => {
                            alrat(product);
                          }}
                        >
                          Add to cart
                        </a>
                        <i className="fas fa-long-arrow-alt-right"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          );
        })}
      </div>
    </div>
  );
};

export default Product;
