import { Button, Container } from "react-bootstrap";
import Table from "react-bootstrap/Table";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { Cler, deletfromToCart } from "./rtk/slice/Cart_slice";
import "./index.css";
import { Link } from "react-router-dom";
function Cart() {
  const Dispatch = useDispatch();
  const clear = () => {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#d33",
      cancelButtonColor: "#3085d6",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        Swal.fire("Deleted!", "Your file has been deleted.", "success");
        Dispatch(Cler());
      }
    });
  };
  const delet = (product) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        Swal.fire("Deleted!", "Your file has been deleted.", "success");
        Dispatch(deletfromToCart(product));
      }
    });
  };
  const cart = useSelector((state) => state.cart);
  const totilprice = cart.reduce((acc, product) => {
    acc += product.price * product.quntity;
    return acc;
  }, 0);
  if (cart.length === 0) {
    return (
      <>
        <Container className="pb-5">
          <h1 className="pt-5 fw-bold color-cart"> Welcome to cart </h1>
          <div className="pt-5">
            <div className="divbtnship pt-5">
              <h2 className="fw-bold">Your cart is empty !</h2>
            </div>
            <div className="divbtnship">
              <Link to="/products" className=" btn btn-success mt-3 ">
                SHOP NOW
              </Link>
            </div>
          </div>
        </Container>
      </>
    );
  } else {
    return (
      <>
        <Container className="pb-5">
          <h1 className="pt-5 fw-bold color-cart"> Welcome to cart </h1>
          <h3 className="py-3 fw-bold price">
            {" "}
            price cart = <span>({totilprice.toFixed(2)} $) </span>{" "}
          </h3>
          <button
            type="button"
            onClick={() => {
              clear();
            }}
            className="btn clear  px-5  mb-4"
          >
            clear
          </button>

          <div>
            <table id="customers">
              <thead>
                <tr className="text-center ">
                  <th>title</th>
                  <th>image</th>
                  <th> price</th>
                  <th>quntity</th>
                  <th>Actone</th>
                </tr>
              </thead>
              <tbody>
                {cart.map((product) => {
                  return (
                    <tr key={product.id} className="text-center fw-bold ">
                      <td>{product.title}</td>
                      <td>
                        <img
                          style={{ height: "100px", width: "100px" }}
                          src={product.image}
                        />
                      </td>
                      <td>{product.price} $</td>
                      <td>{product.quntity}</td>

                      <td>
                        <Button
                          className="btncart"
                          onClick={() => {
                            delet(product);
                          }}
                        >
                          Delete
                        </Button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </Container>
      </>
    );
  }
}

export default Cart;
