import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { addToCart } from "./rtk/slice/Cart_slice";

import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
const About = () => {
  const params = useParams();
  const [product, setproduct] = useState([]);
  useEffect(() => {
    fetch(`http://localhost:9000/Products/${params.path}`)
      .then((res) => res.json())
      .then((json) => setproduct(json));
  }, []);
  const alrat = (product) => {
    Swal.fire("Good job!", "Item placed successfully!", "success");
    dispatch(addToCart(product));
  };
  const dispatch = useDispatch();

  return (
    <div className="py-5 container" key={product.id}>
      <div className="row container">
        <div className="col-lg-6 card container ">
          {
            <img
              src={product.image}
              className="card-img-top img-fluid "
              alt={product.title}
              width="250px"
              height="250px"
            />
          }
        </div>
        <div className="col-lg-6 py-5 card container ">
          <h3 className="card-title mb-3 fw-bold w-100">{product.title}</h3>

          <p className=" h3 fw-bold ">${product.price}</p>

          <div className="w-100 bgcolor py-2">{product.category}</div>
          <div className="pt-3">{product.description}</div>
          <div class="input-group mb-3 pt-4 ">
            <select class="custom-select w-100" id="inputGroupSelect02">
              <option value="3">XXL</option>
              <option value="3">XL</option>
              <option value="3">L</option>
              <option value="2">M</option>
              <option value="1">S</option>
            </select>
          </div>
          <div className="info pt-4">
            <a
              className="btn btn-primary btn-lg px-5 "
              onClick={() => {
                alrat(product);
              }}
            >
              Add to cart
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
